/* RESETS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  font-family: sans-serif;
}

/* CUSTOM */

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 3%;
  height: 50px;
  margin-top: 5px;
}

nav a {
  color: #929BA0;
  text-decoration: none;
}

nav a:hover {
  color: #462920;
}

nav a:not(:last-child) {
  margin-right: 10px;
}

nav a.active {
  color: #462920;
}

h1 {
  color: #462920;
  text-align: center;
  margin: 20px;
}

p {
  color: #462920;
}

a {
  text-decoration: none;
  color: #fff;
}

button {
  background-color: rgb(128, 108, 84);
  color: #fff;
  height: 40px;
  border: none;
  border-radius: 5px;
  width: 100px;
  margin: 7px;
}

button:hover {
  opacity: 0.7;
}

form {
  width: auto;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

form > * {
  padding: 7px 10px;
  margin-bottom: 7px;
  border: 1px solid #d8cec4;
  outline: none;
  color: #462920;
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 30px;
  background-color: #fff;
  text-align: center;
}


@media (max-width: 768px) {
  header {
    font-size: 12px;
  }
  
}